.equipment {
  color: white;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;

  width: 100vw;

  flex-shrink: 0;
  position: relative;
  background-color: rgba(0, 0, 0, 0.3);
}
.equipment::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: white;
  opacity: 0.1;
  z-index: -1;
}

.equipment__title {
  font-size: 2rem;
  margin-bottom: 60px;
}

.equipment__container {
  display: flex;
  gap: 20px;
  width: 50vw;
  padding: 70px 0 70px 0;
  box-sizing: border-box;
}

.equipment__list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  max-width: 768px;
}
.equipment__item {
  display: flex;
  flex-direction: column;
  border-radius: 50%;
}

.equipment__info {
  width: 284px;
  height: 100px;
  background-color: #4e4d4d;
  display: flex;
  justify-content: center;
  align-items: center;
}

.equipment__wrapper {
  display: flex;
  width: 100%;

  align-items: center;
}

.neumann {
  width: 50vw;
  height: 100%;
}
